*,
*:before,
*:after {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

.container {
    margin: 0 auto;
    padding: 0px 10px;
    max-width: 1180px;
}

.header {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 50;
    background-color: rgb(255, 255, 255);
}

.headerBody {
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center;
}

.headerLogo {
    flex: 0 0 70px;
    border-radius: 50%;
    margin-left: 10px;
    overflow: hidden;
    position: relative;
    z-index: 3;
}

.headerLogo img {
    max-width: 100%;
    display: block;
}

.headerList {
    display: flex;
    position: relative;
    z-index: 2;
    margin-right: 10px;
}

.headerList li {
    list-style: none;
    margin: 0px 0px 0px 20px;
}

.headerLink {
    color: #000;
    text-transform: uppercase;
    font-size: 18px;
    text-decoration: none;
}


