body {
    height: 100%;
    max-width: 100%;
    font-family: Arial, "Hevetica Neue", Helvetica, sans-serif;
    background-color: rgba(51, 50, 50, 0.541);

}
.app-wrapper {
    display: grid;
    max-width: 1180px;
    height: 100%;
    margin: 0 auto;
    grid-template-areas: "h h" "c c";
    grid-template-rows: 80px 1fr;
    grid-template-columns: 0fr 10fr;
}

.app-wrapper-content {
    grid-area: c;
}