
.page {
    display: grid;
    grid-template-areas: "list act getUser";
    grid-template-columns: 3fr 2fr 5fr;
    padding: 1em 0em 0em 0em;

}

.userList {
    position: relative;
}

.users {
    grid-area: list;
    display: grid;
    grid-template-areas: "img inf";
    grid-template-columns: 1fr 4fr;
    grid-gap: 1em;
    padding-bottom: 2em;
    padding-left: 1em;
}

.userInfo {    
    grid-area: inf;
}

.userImage {
    grid-area: img;
    height: 75px;
    width: 75px;
    border-radius: 50%;

}

.userAct {
    grid-area: act;
}
.textareas div {
    margin-bottom: 5px;
}
.textareas input {
    margin-bottom: 1em;
    width: 140px;
    height: 20px;
}
.buttons button {
    height: 25px;
    width: 140px;
    margin-bottom: 5px;
    background-color: rgba(61, 61, 61, 0.89);
}
.buttons div {
    margin-top: 1em;
}
.sortFilter {
    position: relative;
}

.getUser{
    grid-area: getUser;

}